@import "src/components/styles/Landing.module";

.services{
  @extend .landing;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;

  .container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 100%;
    margin-top: 5%;

    :first-child{
      margin-left: 0;
    }

    :last-child{
      margin-right: 0;
    }
  }
}