@import "src/style";

.portfolioItem{
  margin: 0.1%;
  background-size: cover;
  background-position: center;
  height: 200px;

  .itemHover{
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(5px);
    color: #FFFFFF;
    padding: 5% 10%;
    cursor: pointer;

    p{
      font-size: 1rem;
      font-weight: bold;
      margin-bottom: 15%;
      box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .tags{
      display: flex;
      flex-wrap: wrap;
      width: 100%;

      span{
        background: rgba(255, 255, 255, 0.7);
        color: #000000;
        padding: 1% 1.5%;
        font-size: 0.75rem;
        font-weight: bold;
        margin: 1% 1% 0 0;
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
      }
    }
  }
}