@import "src/style";

.navbar{
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(5px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  width: 100%;
  padding: 0 $horizontal-padding;
  position: fixed;
  top: 0;
  z-index: 9999;

  .menu{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;

    :last-child{
      margin-right: 0;
    }
  }
}